/* import files */
/*--- common ---*/
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
html {
  background-color: #3a4756;
}
body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
  font-size: 16px;
  font-family: "Open Sans", sans-serif;
}
a {
  text-decoration: none;
}
.clearfix:after {
  content: "";
  display: table;
  clear: both;
}
.container {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
}
.attribution-link {
  margin-bottom: 20px;
  font-size: 12px;
  font-style: italic;
  color: white;
  text-align: center;
}
.attribution-link a {
  color: white;
}
.attribution-link a:hover {
  text-decoration: underline;
}
.news-source {
  margin-bottom: 20px;
  font-size: 32px;
  font-style: italic;
  color: white;
  text-transform: uppercase;
  text-align: center;
}
.news-container {
  display: flex;
  justify-content: space-around;
  width: 100%;
  flex-wrap: wrap;
}
/*--- source-list ---*/
.source-list {
  display: flex;
  justify-content: space-around;
  padding: 0;
  margin: 0;
  padding-bottom: 20px;
  list-style: none;
}
.source-list__item {
  color: white;
  cursor: pointer;
}
.source-list__item:hover {
  color: #dea6a6;
  transition: color 0.3s;
}
/*--- \source-list ---*/
/* news-list-item */
.news-list-item {
  position: relative;
  padding: 0 30px;
  width: 500px;
  min-height: 400px;
  margin-bottom: 30px;
}
@media only screen and (max-width: 61.25em) {
  .news-list-item {
    width: 100%;
  }
}
/*-- news-list-item__content */
.news-list-item__content {
  margin: 0;
  padding: 0 15px;
}
.news-list-item__content li {
  display: inline-block;
}
.news-list-item__content a {
  color: #fff;
}
/*-- news-list-item__img */
.news-list-item__img {
  position: absolute;
  z-index: 1;
  width: calc(100% - 60px);
  height: 100%;
  filter: brightness(70%);
  transition: filter 0.15s ease-in;
  background-size: cover;
}
.news-list-item:hover .news-list-item__img {
  filter: brightness(40%);
}
/*-- news-list-item__wrapper */
.news-list-item__wrapper {
  position: relative;
  padding: 0 15px;
  min-height: 400px;
  overflow: hidden;
  background-color: #fff;
  box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.2);
}
.news-list-item__wrapper:hover .news-list-item__data {
  transform: translateY(0);
}
.news-list-item__data {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  transform: translateY(calc(70px + 1em));
  transition: transform 0.3s;
}
.news-list-item__month {
  text-transform: uppercase;
}
.news-list-item__month,
.news-list-item__year {
  font-size: 0.75em;
}
.news-list-item__header {
  padding: 25px 0;
  color: white;
}
.news-list-item__author {
  font-size: 0.75em;
}
.news-list-item__title {
  margin-top: 10px;
  margin-bottom: 20px;
  font-size: 2em;
  font-weight: 300;
}
@media only screen and (max-width: 61.25em) {
  .news-list-item__title {
    font-size: 1.75em;
  }
}
.news-list-item__description {
  height: 70px;
  margin: 0;
  line-height: 1.3rem;
  font-size: 1em;
}
@media only screen and (max-width: 61.25em) {
  .news-list-item__description {
    font-size: 0.75em;
  }
}
.news-list-item__wrapper {
  background-color: transparent;
  z-index: 2;
}
.news-list-item__wrapper:hover .news-list-item__content span {
  transform: translate(-50%, -10px);
  opacity: 1;
}
.news-list-item__date {
  font-size: 0.75em;
}
.news-list-item__content li {
  position: relative;
  margin: 0 5px;
}
.news-list-item__content span {
  transition: all 0.3s;
  opacity: 0;
}
.news-list-item__data {
  color: white;
  transform: translateY(calc(70px + 4em));
}
.news-list-item__title a {
  color: white;
}
.news-list-item__button {
  position: relative;
  display: block;
  width: 100px;
  margin: 10px auto 40px;
  font-size: 12px;
  font-weight: 700;
  text-align: center;
  color: white;
}
.news-list-item__button::after {
  content: '\2192';
  opacity: 0;
  position: absolute;
  right: 0;
  top: 50%;
  transform: translate(0, -50%);
  transition: all 0.3s;
}
.news-list-item__button:hover::after {
  transform: translate(5px, -50%);
  opacity: 1;
}



